@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');
:root{
  --sidebar-flex:1.5;
  --container-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:2.2rem;
  --name-font:3rem;
}
*
{
  margin:0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
}
body{
  font-family: 'Raleway';
}
.App
{
  display: flex;
  width: 100%;
  min-height: 100vh;
}
nav
{
  padding:60px 30px 0px 30px ;
  flex:var(--sidebar-flex);
  /* background: rgb(238,103,143); */
  /* background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%); */
  background: #ECF0F1;
}
.container
{
  padding: 100px ;
  background-color: #f4f5f9;
  flex:var(--container-flex);
  box-shadow: 10px 10px 5px #aaa;
  margin: 10px;
  width: 82%;
}

.home
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  
}
.text-center{
  text-align: center;
}
#colorlib-aside .author-img {
  width: 150px;
  height: 150px;
  margin: 0 auto;
  margin-bottom: 30px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%; }
.profilepic
{
  border-radius: 50%;
  width: 200px;
  margin-top: 40px;
  /* margin: 30px; */
}
.coverImg
{
  width:100%;
  height:500px;
}
.aboutMe
{
  font-size: var(--typing-font);
  margin-bottom: 10px;
}
.nameMe
{
  font-size: var(--name-font);
  margin-bottom: 30px;
}

nav ul{
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  /* color: white !important; */
}
nav ul li
{
  margin:40px;
}

a
{
  text-decoration: none;
  color: #34495E;
}

/* .subtopic
{
  margin:10px 10px 30px 10px !important;
} */
p{
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}

.social i
{
  padding-right: 30px;
  font-size: 30px;
  color: #707B7C;
  margin-top: 40px;
}
.social i:hover
{
  color: teal;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  .container
  {
    padding: 50px ;
  }
  nav ul
  {
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    bottom: 30px;
    margin-top: 60px !important;
  }
  .social i
  {
    padding-right: 0px;
  }
}

.background-color{
  width: 45%;
  background-color:#f4ece5;
  clear: both;
}

.content-box{
  width: 1000px;
  /* position: relative;
  border:1px solid black; */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  height: 650px;
  z-index: 3;
  margin-left: -450px !important;
  margin-top: 80px;
}

.colored-box{
  background-color: #f4ece5;
}

.name{
    font: normal normal bold 27px/1.4em poppins-semibold,poppins,sans-serif;
    color: #000000;
    font-weight: bold;
    font-size: 40px;
}

.hl{
  border-top: 4px solid teal;
  width: 200px;
}
.name-cover{
  line-height: 1.2em;
    text-align: center;
  margin-top: 30px;
  padding-bottom: 30px;
  border-bottom: 3px solid teal;
}
.position{
  letter-spacing: 0.25em;
  font: normal normal normal 17px/1.4em avenir-lt-w01_35-light1475496,sans-serif;
  color: #000000;
  font-size: 20px;
}
.position-cover{
  line-height: 1.2em;
    text-align: center;
  margin-top: 30px;
  padding-bottom: 30px;
  
}

.header{
  font: normal normal bold 27px/1.4em poppins-semibold,poppins,sans-serif;
    text-align: left;
  font-size: 60px;
  margin-top: 40px !important;
  font-weight: bold;
}

.subtopic{
  font: normal normal bold 27px/1.4em poppins-semibold,poppins,sans-serif;
  font-size: 30px;
  line-height: 1.2em;
  text-align: left;
  font-weight: 600;
  margin-top: 35px;
  margin-bottom: 35px;
}
.buttons{
  text-align: center;
  border-radius: 100px;
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 30px;
  height: 35px;
  background-color: white;
  border: 1px solid #cccccc;
  width: 160px !important;
  height: 40px !important;
  transition-duration: 0.5s;
}

.buttons:hover{
  background-color: teal;
  color: white;
}
.description{
  margin-top: 35px;
  line-height: 1.5em;
  font-size: 16px;
  font: normal normal bold 27px/1.4em sans-serif;
  font-weight: 100;
}

.project-buttons{
  background-color: teal;
  color: white;
}
.project-buttons:hover{
  background-color: white;
  color: black;
}

.box-design{
  background-color:#f4ece5;
  height: 200px;
  width: 200px;
  margin-left: -100px;
}

.project-background{
  background-color:#f4ece5;
  height: 100%;
  width: 100%;
}

.project-header{
  font-weight: 600;
  font: normal normal bold 27px/1.4em poppins-semibold,poppins,sans-serif;
  line-height: 0.95em;
  font-size: 50px;
  height: 60px;
  transition-property: width;
  transition-duration: 2s;
  transition-timing-function: ease-in;
  transition-delay: 1s;
}

.background-animation{
  margin-left: 37%;
  background: white;
  width: 300px;
  
}

.background-animation:hover{
  width: 500px;
  background: teal;
  transition-property: width;
  transition-duration: 2s;
  transition-timing-function: ease-in;
  transition-delay: 1s;
  
}

.content-box-projects{
  width: 1000px;
